import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';
import CardApplicationAuto from '../../../../Views/CardApplicationAuto';
import Message from '../../../../Views/Messages/Message/Message';
import Pagination from '../../../../Views/Pagination/Pagination';
import { smoothTop } from '../../../../helpers';

const AutoDocsEditList = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('tg');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {
        state?.textMessage
          ? setMessageInForm((c) => ({
              ...c,
              textMessage: state?.textMessage,
              colorMessage: state?.colorMessage ?? "#555050",
            }))
          : console.log("fake");
      }, [state?.textMessage, state?.colorMessage]);
    
    useEffect(() => {
        dispatch('setTextHeader', state?.name_button ?? 'РЕДАКТИРОВАТЬ ДОКУМЕНТЫ');
        dispatch('setActiveHeaderText', true);
        tg.BackButton.show();
        tg.BackButton.onClick(() => navigate('/button_docs', {
            state: {
                name_button: state?.old_name_button ?? 'ФАНТИКИ'
            }
        }));

        const paramsList = {
            url: '/api_get_docs/',
            page: 1,
            page_size: 30,
            dataRequst: res => {

                if (
                    res?.result?.info?.status !== undefined &&
                    !res?.result?.info?.status
                  ) {
                    setMessageInForm((c) => ({
                      ...c,
                      textMessage: res?.result?.info?.message,
                      colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
                    }));
                    smoothTop();
                    return;
                  }
                    const copyData = res?.result?.results.slice();
                    setListAplication(c => ({
                        ...c,
                        ...res.result,
                        results: [...copyData]
                    }))
            }
        };
        dispatch('getListApplications', paramsList)
    }, [dispatch, state?.action_page_id, tg.BackButton, navigate]);

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_get_docs/',
            page: page ?? 1,
            page_size: 30,
            setFieldValue: () => { },
            dataRequst: res => {
                if (
                    res?.result?.info?.status !== undefined &&
                    !res?.result?.info?.status
                  ) {
                    setMessageInForm((c) => ({
                      ...c,
                      textMessage: res?.result?.info?.message,
                      colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
                    }));
                    smoothTop();
                    return;
                  }
                    const copyData = res?.result?.results.slice();
                    setListAplication(c => ({
                        ...c,
                        ...res.result,
                        results: [...copyData]
                    }))
            },
            redirectTo: () => { }
        };
        dispatch('setDataCreateRequest', paramsList)
    }    

    return (
        <React.Fragment>
            {
                listAplication.results.length ?
                    <React.Fragment>
                        <Message
                            message={messageInForm.textMessage}
                            colorMessage={messageInForm.colorMessage}
                        />
                        <CardApplicationAuto
                            listAplication={listAplication}
                            pathGoTo={'/button_docs/button_edit_doc_form'}
                        />

                        {
                            listAplication.count > 30 ?
                                <Pagination
                                    data={listAplication.results}
                                    allCount={listAplication.count}
                                    count={30}
                                    handlerChangePaginations={changePaginationPage}
                                    currentPage={Number(listAplication.current_page)}
                                    location={'center'}
                                />
                                : null
                        }
                        <Offset mb={50} />

                    </React.Fragment>
                    : <Spinner
                        text={'У Вас нет пока заявок'}
                    />
            }

        </React.Fragment>
    )
}
export default AutoDocsEditList;