import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { setDataButtonBotton, smoothTop } from "../../helpers";

import ListMainButton from "../../Views/ListMainButton/ListMainButton";
import Message from "../../Views/Messages/Message/Message";
import Offset from "../../Views/Offset";
import Spinner from "../../Views/Spinner";

const CreateRequest = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, contextCreateRequest, tg } = useStoreon(
    "contextCreateRequest",
    "tg"
  );
  const { message, buttons } = contextCreateRequest;
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    setDataButtonBotton(false, tg);
    state?.textMessage
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: state?.textMessage,
          colorMessage: state?.colorMessage ?? "#555050",
        }))
      : message?.name
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: message.name,
          colorMessage: message?.colorMessage ?? "#555050",
        }))
      : console.log("fake");
  }, [message?.name, state?.textMessage]);

  useEffect(() => {
    dispatch("openModal", { show: false });
    dispatch("setTextHeader", state?.name_button ?? "СОЗДАТЬ ЗАПРОС");
    dispatch("setActiveHeaderText", true);
    tg.BackButton.show();
    tg.BackButton.onClick(() => navigate("/"));

    const params = {
      url: "/api_get_page/",
      page_id: state?.action_page_id ?? 2,
      dataRequst: (res) => {
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
      },
    };
    dispatch("getContextCreateRequest", params);
  }, [dispatch, tg.BackButton, navigate]);

  return (
    <React.Fragment>
      {"buttons" in contextCreateRequest ? (
        <React.Fragment>
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />
          <ListMainButton
            old_name_button={state?.name_button}
            localPath={"/button_make_request/"}
            dispatch={dispatch}
            list={buttons}
          />
          <Offset mb={40} />
        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};
export default CreateRequest;
