import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { setDataButtonBotton, smoothTop } from "../../helpers";
import Icon from "../../Views/Icon/Icon";
import Block from "../../Views/ListMainButton/DetaliBlock";
import Message from "../../Views/Messages/Message/Message";
import Spinner from "../../Views/Spinner";

import style from "./styles/myprofile.module.scss";

const MyProfile = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [contextPage, setContextPage] = useState({ message: "", buttons: [] });
  const { dispatch, context, tg } = useStoreon("context", "tg");
  const { buttons } = contextPage;
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    setContextPage((c) => ({ ...c, ...context.children }));
  }, [context?.children]);

  useEffect(() => {
    state?.textMessage
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: state?.textMessage,
          colorMessage: state?.colorMessage ?? "#555050",
        }))
      : contextPage.message
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: contextPage.message,
        }))
      : console.log("fake");
  }, [contextPage.message, state?.textMessage]);

  useEffect(() => {
    setDataButtonBotton(false, tg);
    dispatch("setTextHeader", state?.name_button ?? 'ПОДПИСКА');
    dispatch("setActiveHeaderText", true);

    tg.BackButton.show();
    tg.BackButton.onClick(() => navigate("/"));

    const params = {
      url: "/api_get_context_profile/",
      page_id: state?.action_page_id ?? 31,
      dataRequst: (res) => {
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
      },
    };
    dispatch("getContext", params);
  }, [dispatch, tg.BackButton, navigate]);

  return (
    <React.Fragment>
      {buttons?.length ? (
        <React.Fragment>
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />

          <Block.BlockContainerList>
            {buttons.map((el,i) => {
              return (
                <Block.ItemList key={i} {...el}>
                  <Link
                    to={`/button_my_profile${el?.action ?? "/" + el?.slug}`}
                    state={{
                      ...context?.children?.buttons[0]?.children,
                      old_name_button: state?.name_button,
                      auto_payment: state?.auto_payment
                        ? state.auto_payment
                        : context.auto_payment,
                    }}
                    onClick={() =>
                      el.slug
                        ? dispatch("setActiveHeaderText", true)
                        : dispatch("setActiveHeaderText", false)
                    }
                  >
                    <Icon
                      image={el.image}
                      width={50}
                      height={45}
                      className={style["menu__items-image"]}
                    />

                    <Block.ItemName name={el.value ?? el.text} />
                  </Link>
                </Block.ItemList>
              );
            })}
          </Block.BlockContainerList>
        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};
export default MyProfile;
