import React, { useEffect, useState } from "react";
import { useStoreon } from "storeon/react";
import Spinner from "../../Views/Spinner";
import { Link, useLocation, useNavigate } from "react-router-dom";

import style from "./styles/info.module.scss";
import { smoothTop } from "../../helpers";
import Message from "../../Views/Messages/Message/Message";

const Info = () => {
  const { dispatch, tg } = useStoreon("tg");
  const { state } = useLocation();
  const navigate = useNavigate();
  const [buttonsInfo, setButtonsInfo] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button ?? "Информация");
    dispatch("setActiveHeaderText", true);
    tg.BackButton.show();
    tg.BackButton.onClick(() => navigate("/button_my_profile",{
      state: {
        name_button: state?.old_name_button ?? 'подписка'
    }
    }));
  }, [dispatch, state?.action_page_id, tg.BackButton, state?.slug]);

  useEffect(() => {
    const params = {
      url: "/api_get_info/",
      page_id: state?.action_page_id ?? 7,
      dataRequst: (res) => {
        setButtonsInfo(
          res?.result?.buttons?.map((el, i) => {
            return {
              text: el.name,
              slug: i + 1,
              context: el.text,
            };
          })
        );
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
      },
    };
    dispatch("getContextInfo", params);
  }, [dispatch, state?.action_page_id, state?.slug]);

  return (
    <React.Fragment>
        <Message
          message={messageInForm.textMessage}
          colorMessage={messageInForm.colorMessage}
        />
          <div className={style["info__info-container-list"]}>
            {buttonsInfo.length ? (
              <ul className={style["info__container-list"]}>
                {buttonsInfo.map((el, i) => {
                  return (
                    <li key={i} className={style["info__items"]}>
                      <Link
                        to={`/button_my_profile/button_info/${el.slug}`}
                        state={{
                          old_name_button: state?.name_button ?? "Информация",
                          context: el.context,
                          action_page_id: state?.action_page_id ?? 7,
                          title: el.text,
                          slug: el.slug,
                        }}
                        onClick={() =>
                          el.slug
                            ? dispatch("setActiveHeaderText", true)
                            : dispatch("setActiveHeaderText", false)
                        }
                      >
                        <div className={style["info__items-text"]}>
                          {el.text}
                        </div>
                      </Link>
                    </li>
                  );
                })}
              </ul>
            ) : <Spinner />}
          </div>
    </React.Fragment>
  );
};

export default Info;
