export const getOptionsPaymentPeriod = (data, access) => {
  let options = [];
  if (Array.isArray(data) && data.length) {
    data.map((el) => {
      let value_price = "";
      let value_old_price = "";
      if (access === "full") {
        if (el.payment_price_full_without_discount) {
          value_old_price = el.payment_price_full_without_discount;
          value_price = el.payment_price_full;
        }
        value_price = el.payment_price_full; // + '₽'
      } else {
        if (el.payment_price_full) {
          value_old_price = el.payment_price_limited_without_discount;
          value_price = el.payment_price_limited;
        }
        value_price = el.payment_price_limited;
      }
      return options.push({
        title_price: value_price,
        title_old_price: value_old_price,
        title: el.value,
        value: el.value || "",
        image: el?.image || "",
        key_value: el.key_value,
      });
    });
  }
  return options;
};

export const getCookie = (name) => {
  try {
    let nameEQ = name + "=";
    let ca = document.cookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") c = c.substring(1, c.length);
      if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
    }
    return null;
  } catch (e) {
    return null;
  }
};
export const setCookie = (name, value, days) => {
  let expires = "";
  if (days) {
    var date = new Date();
    date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/;";
};

export const removeCookie = (name) => {
  const CookiesDelete = (name) => {
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i];
      const eqPos = cookie.indexOf("=");
      const names = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
      document.cookie = names + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT;";
      document.cookie =
        names + "=; path=/; expires=Thu, 01 Jan 1970 00:00:01 GMT;";
    }
  };
  CookiesDelete(name);
};

export const rus_to_latin =  ( str ) => {
    
  var ru = {
      'а': 'a', 'б': 'b', 'в': 'v', 'г': 'g', 'д': 'd', 
      'е': 'e', 'ё': 'e', 'ж': 'j', 'з': 'z', 'и': 'i', 
      'к': 'k', 'л': 'l', 'м': 'm', 'н': 'n', 'о': 'o', 
      'п': 'p', 'р': 'r', 'с': 's', 'т': 't', 'у': 'u', 
      'ф': 'f', 'х': 'h', 'ц': 'c', 'ч': 'ch', 'ш': 'sh', 
      'щ': 'shch', 'ы': 'y', 'э': 'e', 'ю': 'u', 'я': 'ya',
  'ъ': 'ie', 'ь': '', 'й': 'i'
  }, n_str = [];
      
  for ( var i = 0; i < str.length; ++i ) {
     n_str.push(
            ru[ str[i] ]
         || ru[ str[i].toLowerCase() ] == undefined && str[i]
         || ru[ str[i].toLowerCase() ].replace(/^(.)/, function ( match ) { return match.toUpperCase() })
     );
  }
  
  return n_str.join('');
}

export const exclusion = (path) => {
  if(path.includes('/button_my_profile/button_info/')) return false
  if(path.includes('/button_filters/unit/')) return false
  if(path.includes('/button_filters/respair/')) return false
  if(path.includes('/button_filters/spare/')) return false
  switch(path){
    case '/button_garant_members_info':
      return false    
    case '/button_my_profile/menu_profile': //???
      return false    
    case '/button_parsing/button_edit_parsing_form':
      return false    
    case '/button_docs/button_edit_doc_form':
      return false    
    case '/button_sales/button_edit_sales_form':
      return false    
    case '/button_filters/respair':
      return false    
    default:
      return true
  }
}

