import React, { useEffect, useState } from "react";
import { useStoreon } from "storeon/react";
import Spinner from "../../Views/Spinner";
import { useLocation, useNavigate } from "react-router-dom";
import Message from "../../Views/Messages/Message/Message";
import ListMainButton from "../../Views/ListMainButton/ListMainButton";
import { setDataButtonBotton, smoothTop } from "../../helpers";

const Rating = () => {
  const { dispatch, contextRaiting, tg } = useStoreon("contextRaiting", "tg");
  const { state } = useLocation();
  const navigate = useNavigate();
  const { message, buttons } = contextRaiting;
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    state?.textMessage
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: state?.textMessage,
          colorMessage: state?.colorMessage ?? "#555050",
        }))
      : message?.name
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: message.name,
          colorMessage: message?.colorMessage ?? "#555050",
        }))
      : console.log("fake");
  }, [message?.name, state?.textMessage]);

  useEffect(() => {
    setDataButtonBotton(false, tg);
    dispatch("setTextHeader", state?.name_button ?? 'РЕЙТИНГ И ОТЗЫВЫ');
    dispatch("setActiveHeaderText", true);
    tg.BackButton.show();
    tg.BackButton.onClick(() => navigate("/"));
    const params = {
      url: "/api_get_page/",
      page_id: state?.action_page_id ?? 5,
      dataRequst: res => {
        if ( res?.result?.info?.status !== undefined && !res?.result?.info?.status) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message ?? '',
            colorMessage: res?.result?.info?.status? "#555050" : "#ff0000",
          }));
          smoothTop();
          return
        }
      },
    };
    dispatch("getContextRaiting", params);
  }, [dispatch, state?.action_page_id, tg.BackButton, navigate]);

  return (
    <React.Fragment>
      {"buttons" in contextRaiting ? (
        <React.Fragment>
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />
          {buttons.length ? (
            <ListMainButton
              old_name_button={state?.name_button}
              localPath={"/button_rating/"}
              dispatch={dispatch}
              list={buttons}
            />
          ) : null}
        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};

export default Rating;
