import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { getOptionsFilters, smoothTop } from "../../../../helpers";
import CardFilters from "../../../../Views/CardFilters";
import Message from "../../../../Views/Messages/Message/Message";

const FiltersTransport = () => {
  const type_list = "truck";
  const { state } = useLocation();
  console.log({ state });
  const navigate = useNavigate();
  const message = state?.dataFilters?.message;
  const { dispatch, tg } = useStoreon("tg");
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [selectProduct, setSelectProduct] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button ?? 'ГРУЗОПЕРЕВОЗКИ');
    dispatch("setActiveHeaderText", true);
    tg.BackButton.show();
    tg.BackButton.onClick(() => navigate("/button_filters", {
      state: {
        name_button: state?.old_name_button ?? 'ФИЛЬТРЫ'
    }
    }));

    const params = {
      url: "/api_get_filters/",
      type: state?.type ?? type_list,
      dataRequst: (res) => {       
        const copyData = [
          {
            ...res?.result,
            name: "Грузоперевозки",
            id: 0,
          },
        ]?.slice();
        setDataRequestBrand((c) => [...getOptionsFilters(copyData)]);
        const countEnable = copyData
          .map((el) => (el.status ? el?.id : null))
          .filter((el) => el !== null);
        if (countEnable.length > 0) setSelectProduct((c) => [...countEnable]);
        // if (
        //   res?.result?.info?.status !== undefined &&
        //   !res?.result?.info?.status
        // ) {
        //   setMessageInForm((c) => ({
        //     ...c,
        //     textMessage: res?.result?.info?.message,
        //     colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
        //   }));
        //   smoothTop();
        //   return;
        // }
      },
    };
    dispatch("getContext", params);
  }, [dispatch, navigate, tg.BackButton]);

  useEffect(() => {
    setMessageInForm((c) => ({
      ...c,
      textMessage: message,
    }));
  }, [message]);

  const handlerChangeData = (changeData) => {
    const item = +changeData.value;
    if (selectProduct.includes(item)) {
      setSelectProduct((c) => c.filter((el) => el !== item));
    } else {
      setSelectProduct((c) => [...c, item]);
    }

    let params = {
      url: "/api_set_filters/",
      type: state?.type ?? type_list,
      dataRequst: (res) => {
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
      },
      redirectTo: () => {},
    };
    dispatch("setDataCreateRequest", params);
  };

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      <CardFilters
        list={dataRequstBrand}
        handlerChangeData={handlerChangeData}
        localPath={`/button_filters/${state?.type}/`}
        old_state={state?.dataFilters?.children}
        selectProduct={selectProduct}
        name_button={state?.name_button}
        type_list={type_list}
        type={state?.type}
      />
    </React.Fragment>
  );
};
export default FiltersTransport;
