import {
  createBrowserRouter,
  createRoutesFromElements,
  Route,
} from "react-router-dom";

import CreateRequest from "../Pages/CreateRequest";

import Layout from "../Pages/Layuot/Layout";
import Main from "../Pages/Main/Main";
import EmptyPege from '../Pages/EmptyPege';

import AggregateRequestUnit from "../Pages/CreateRequest/DetaliPages/AggregateRequestUnit";
import AggregateRequestSpare from "../Pages/CreateRequest/DetaliPages/AggregateRequestSpare";
import AggregateRequestRespair from "../Pages/CreateRequest/DetaliPages/AggregateRequestRespair";
import AggregateRequestTruck from "../Pages/CreateRequest/DetaliPages/AggregateRequestTruck";
import RequestTransport from "../Pages/CreateRequest/DetaliPages/RequestTransport";

import CreateInParsing from "../Pages/CreateInParsing";
import AddAutoParsing from "../Pages/CreateInParsing/DetaliPages/AddAutoParsing"; //from "../Pages/CreateInParsing/DetaliPages/AddAutoParsing";
import CreateEditParserForm from "../Pages/CreateInParsing/DetaliPages/CreateEditParserForm";
import CreateEditParserList from "../Pages/CreateInParsing/DetaliPages/CreateEditParserList";
import CreateDeleteParserList from "../Pages/CreateInParsing/DetaliPages/CreateDeleteParserList";
import CreateSearchParserList from "../Pages/CreateInParsing/DetaliPages/CreateSearchParserList";

import Info from "../Pages/Info";

import Docs from "../Pages/Docs";
import AddAutoDocs from "../Pages/Docs/DetaliPages/AddAutoDocs/AddAutoDocs";
import AutoDocsEditForm from "../Pages/Docs/DetaliPages/AutoDocsEditForm";
import AutoDocsEditList from "../Pages/Docs/DetaliPages/AutoDocsEditList";
import AutoDocsSearchList from "../Pages/Docs/DetaliPages/AutoDocsSearchList";
import AutoDocsDeleteList from "../Pages/Docs/DetaliPages/AutoDocsDeleteList";

import InfoContext from "../Pages/Info/DetaliPages/InfoContext";
import Rating from "../Pages/Rating";
import SeeReview from "../Pages/Rating/DetaliPages/SeeReview";
import CreateReview from "../Pages/Rating/DetaliPages/CreateReview";
import TopReview from "../Pages/Rating/DetaliPages/TopReview";

import OurWarrantly from "../Pages/Rating/DetaliPages/OurWarrantly";
import OurWarrantlyAdmin from "../Pages/Rating/DetaliPages/OurWarrantly/OurWarrantlyAdmin";
import OurWarrantlyMembers from "../Pages/Rating/DetaliPages/OurWarrantly/OurWarrantlyMembers";
import CreateInSales from "../Pages/CreateInSales";

import AddAutoSales from "../Pages/CreateInSales/DetaliPages/AddAutoSales";
import CreateEditSalesList from "../Pages/CreateInSales/DetaliPages/CreateEditSalesList";
import CreateEditSaleForm from "../Pages/CreateInSales/DetaliPages/CreateEditSaleForm";
import CreateDeleteSaleList from "../Pages/CreateInSales/DetaliPages/CreateDeleteSaleList";
import EmiratesPege from "../Pages/EmiratesPege";
import MyProfile from "../Pages/MyProfile";
import Payment from "../Pages/MyProfile/DetaliPage";
import Filters from "../Pages/Filters/Filters";
import FiltersSpare from "../Pages/Filters/DetaliPages/FiltersSpare";
import MyAdvert from "../Pages/MyAdvert";
import FiltersSpareModels from "../Pages/Filters/DetaliPages/FiltersSpare/FiltersSpareModels";
import FiltersSpareGenerations from "../Pages/Filters/DetaliPages/FiltersSpare/FiltersSpareGenerations";
import FiltersTruck from "../Pages/Filters/DetaliPages/FiltersTruck/FiltersTruck";
import FiltersTransport from "../Pages/Filters/DetaliPages/FiltersTransport";
import FiltersRespair from "../Pages/Filters/DetaliPages/FiltersRespair/FiltersRespair";
import FiltersRespairSities from "../Pages/Filters/DetaliPages/FiltersRespair/FiltersRespairSities";
import FiltersUnit from "../Pages/Filters/DetaliPages/FiltersUnit/FiltersUnit";
import FiltersUnitModels from "../Pages/Filters/DetaliPages/FiltersUnit/FiltersUnitModels";
import FiltersUnitGenerations from "../Pages/Filters/DetaliPages/FiltersUnit/FiltersUnitGenerations";
import OutWarrantlyMembtrsInfo from "../Pages/Rating/DetaliPages/OurWarrantly/OurWarrantlyMembers/OutWarrantlyMembtrsInfo/OutWarrantlyMembtrsInfo";
// import AddAutoDocs from "../Pages/CreateInParsing/DetaliPages/AddAutoDocs";



export const router = createBrowserRouter(
  createRoutesFromElements(
    <Route path="/" element={<Layout />}>
      <Route path="/" element={<Main />} />


        <Route path="button_make_request" element={<CreateRequest />} /> // СОЗДАТЬ ЗАПРОС +
          <Route path="/button_make_request/button_request_unit" element={<AggregateRequestUnit />} /> //Запрос на агрегат +
          <Route path="/button_make_request/button_request_spare" element={<AggregateRequestSpare />} /> //ЗАПРОС НА ЗАПЧАСТЬ  +
          <Route path="/button_make_request/button_request_respairs" element={<AggregateRequestRespair />} /> //ЗАПРОС НА РЕМОНТ +
          <Route path="/button_make_request/button_request_transport" element={<RequestTransport />} /> //ЗАПРОС НА ГРУЗОВЫЕ  +
          <Route path="/button_make_request/button_request_trucks" element={<AggregateRequestTruck />} /> // ЗАПРОС НА ГРУЗОПЕРЕВОЗКИ +
        
      {/* **************************************** */}

        <Route path="button_my_advert" element={<MyAdvert />} /> //ОБЪЯВЛЕНИЯ +
          
            <Route path="button_parsing" element = { <CreateInParsing /> } /> // АВТО В РАЗБОРЕ +
              <Route path="/button_parsing/button_create_parsing" element={<AddAutoParsing />} /> // Добавить авто в разборе +
              <Route path="/button_parsing/button_search_parsing" element={<CreateSearchParserList />} /> //КАТАЛОГ РАЗБОРА +
              <Route path="/button_parsing/button_edit_parsing" element={<CreateEditParserList />} />  // РЕДАКТИРОВАТЬ АВТО +
                <Route path="/button_parsing/button_edit_parsing_form" element={<CreateEditParserForm />} />   // ОБЪЯВЛЕНИЕ НОМЕР №
              <Route path="/button_parsing/button_delete_parsing" element={<CreateDeleteParserList />} />  // УДАЛИТЬ АВТО +
            
            <Route path="button_docs" element={<Docs />} /> // ФАНТИКИ +
              <Route path="/button_docs/button_create_doc" element={<AddAutoDocs />} /> //ДОБАВИТЬ ДОКУМЕНТЫ +
              <Route path="/button_docs/button_edit_doc" element={<AutoDocsEditList />} /> //РЕДАКТИРОВАТЬ ДОКУМЕНТЫ +
                <Route path="/button_docs/button_edit_doc_form" element={<AutoDocsEditForm />} /> // +
              <Route path="/button_docs/button_delete_doc" element={<AutoDocsDeleteList />} /> //УДАЛИТЬ ДОКУМЕНТЫ +
              <Route path="/button_docs/button_search_doc" element={<AutoDocsSearchList />} /> //КАТАЛОГ ДОКУМЕНТОВ +
            
            <Route path="button_sales" element = { <CreateInSales /> } /> //АВТО НА ПРОДАЖЕ +
              <Route path="/button_sales/button_create_sale" element={<AddAutoSales />} /> // РАЗМЕСТИТЬ АВТО НА ПРОДАЖУ +
              <Route path="/button_sales/button_edit_sale" element={<CreateEditSalesList />} /> // РЕДАКТИРОВАТЬ СВОИ АВТО НА ПРОДАЖУ +
              <Route path="/button_sales/button_edit_sales_form" element={<CreateEditSaleForm />} /> // +
              <Route path="/button_sales/button_delete_sale" element={<CreateDeleteSaleList />} /> // УДАЛИТЬ СВОИ АВТО НА ПРОДАЖУ
      {/* **************************************** */}

        <Route path="button_rating" element={<Rating />} /> //РЕЙТИНГ И ОТЗЫВЫ +
          <Route path="/button_rating/button_search_rating" element={<SeeReview />} /> //ПОСМОТРЕТЬ ОТЗЫВЫ +
          <Route path="/button_rating/button_create_rating" element={<CreateReview />} /> //НАПИСАТЬ ОТЗЫВ - нет ключа info
          <Route path="/button_rating/button_top_rating" element={<TopReview />} /> //Топ-100 пользователей +
          
          <Route path="/button_rating/button_garant_rating" element={<OurWarrantly />} /> //Наши гаранты +
            <Route path="/button_rating/button_garant_rating/button_garant_admin" element={<OurWarrantlyAdmin />} /> //ГАРАНТ ОТ АДМИНИСТРАЦИИ +
            <Route path="/button_rating/button_garant_rating/button_garant_members" element={<OurWarrantlyMembers />} /> //Наши гаранты участники +
            <Route path="/button_rating/button_garant_rating/button_garant_members_info" element={<OutWarrantlyMembtrsInfo />} /> //Наши гаранты детали +
       

       

          {/* <Route path="/button_sales/button_search_sale" element={<CreateInSales />} /> */}
          <Route path="/button_my_profile" element={<MyProfile />} /> // ПОДПИСКА +
            <Route path="/button_my_profile/menu_profile" element={<Payment />} /> // ПОДПИСКА  +
            <Route path="button_my_profile/button_info" element={<Info />} /> //Информация +
            <Route path="/button_my_profile/button_info/:id" element={<InfoContext />} /> // Информация детали


        <Route path="button_emirates" element={<EmiratesPege />} />

        <Route path="button_filters" element={<Filters />} /> // ФИЛЬТРЫ +

          <Route path="/button_filters/spare" element={<FiltersSpare />} /> //Агрегаты и запчасти +
            <Route path="/button_filters/spare/:id" element={<FiltersSpareModels />} /> // +
              <Route path="/button_filters/spare/:id/:id" element={<FiltersSpareGenerations />} /> // +

          <Route path="/button_filters/unit" element={<FiltersUnit />} /> //Только Агрегаты +
            <Route path="/button_filters/unit/:id" element={<FiltersUnitModels />} /> // +
              <Route path="/button_filters/unit/:id/:id" element={<FiltersUnitGenerations />} /> // +
        
          <Route path="/button_filters/truck" element={<FiltersTruck />} /> // ГРУЗОВЫЕ +

          <Route path="/button_filters/respair" element={<FiltersRespair />} /> // НА РЕМОНТ +
            <Route path="/button_filters/respair/:id" element={<FiltersRespairSities />} /> // +
          
          <Route path="/button_filters/transport" element={<FiltersTransport />} /> // ГРУЗОПЕРЕВОЗКИ +

          <Route path="/button_filters/profile" element={<MyProfile />} /> // +
          
        <Route path="*" element={<EmptyPege />} />
    </Route>
  )
);