import React, { useEffect } from "react";
import { Outlet, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import ModalWindow from "../../Views/ModalWindow/ModalWindow";
import Header from "../Header/Header";
import { exclusion, getCookie, setCookie } from "../../helpers";


const Layout = ({

}) => {
    const { activeBackMenu, modal, dispatch, tg } = useStoreon('activeBackMenu', 'modal', 'tg');
    const navigate = useNavigate();
    const closeModal = () => {
        dispatch('closeModal')
    }

    useEffect(()=>{
        if(getCookie('lastUrl')){
            navigate(getCookie('lastUrl'));
        }
        if(getCookie('itemMenu')){
            dispatch('setItemMenu',getCookie('itemMenu'))
        }
    },[])

    useEffect(() => {
        closeModal();
         document.querySelector('.goto').scrollIntoView({ block: 'start', behavior: 'smooth' });
        const pathname = window.location.pathname
        if(exclusion(pathname))
            setCookie('lastUrl',pathname)
    }, [window.location.href.split('/').pop()])

    useEffect(() => {

        tg.onEvent('viewportChanged', e => {
            if (!e.isStateStable) {
                const smoothScrollUp = (step) => {
                    // const currentPositionModal = document.querySelector('.modalwindow__content').scrollTop;
                    // document.querySelector('.modalwindow__content').scroll(0, currentPositionModal - step);
                    
                    const currentPosition = document.querySelector('.main-context').scrollTop;
                    document.querySelector('.main-context').scroll(0, currentPosition - step);
                    return () => tg.offEvent('viewportChanged')
                }
                smoothScrollUp(10)
            }
        });

    }, [tg])

    return (
        <React.Fragment>
            <ModalWindow
                show={modal.show}
                content={modal.content}
                addClass={modal.addClass}
                closeModal={closeModal}
            />
            <div className={'container'}>
                <Header />
                <main
                    className={"main-context"}
                    style={{
                    top: activeBackMenu ? `0px` : `-50px`,
                    }}
                >
                    <span className="goto"></span>
                    <Outlet />
                </main>
            </div>
        </React.Fragment>
    )
}

export default Layout;