import { Form, Formik } from "formik";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import {
  activeButtonBootomForConfirm,
  getOptions,
  setDataButtonBottonTrigger,
  smoothTop,
} from "../../../../helpers";
import Button from "../../../../Views/Button/Button";
import Offset from "../../../../Views/Offset";
import Select from "../../../../Views/Select";
import Spinner from "../../../../Views/Spinner";
import Message from "../../../../Views/Messages/Message/Message";
import CardApplication from "../../../../Views/CardApplication";
import Pagination from "../../../../Views/Pagination/Pagination";
import SelectDesktop from "../../../../Views/Select/SelectDesktop";

const CreateSearchParserList = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingGo, setIsLoadingGo] = useState(false);
  const { dispatch, tg } = useStoreon("tg");
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [listAplication, setListAplication] = useState({
    count: 0,
    results: [],
  });
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button ?? "КАТАЛОГ РАЗБОРА");
    dispatch("setActiveHeaderText", true);
    tg.BackButton.show();
    tg.BackButton.onClick(() =>
      navigate("/button_parsing", {
        state: {
          name_button: state?.old_name_button ?? "АВТО В РАЗБОРЕ",
        },
      })
    );
    const params = {
      url: "/api_get_cars/",
      page_id: state?.action_page_id ?? 3,
      type: "brand",
      dataRequst: (res) => {
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
        if (res?.result.length) {
          const copyData = res?.result?.slice();
          setDataRequestBrand((c) => [...c, ...getOptions(copyData)]);
        }
      },
    };
    dispatch("getContext", params);
  }, [
    dispatch,
    tg.BackButton,
    navigate,
    state?.action_page_id,
    state?.old_name_button,
    state?.name_button,
  ]);

  const handlerSubmitSearch = (data, { setFieldValue }) => {
    setIsLoadingGo(true);
    delete data["type"];
    delete data["brand_id"];
    delete data["page_id"];
    delete data["text"];
    const params = {
      ...data,
      confirmShow: true,
      url: "/api_search_parsings/",
      setFieldValue: setFieldValue,
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
        const copyData = res?.result?.results.slice();
        setListAplication((c) => ({
          ...c,
          ...res.result,
          model_id: data.model_id,
          generation_id: data.generation_id,
          results: [...copyData],
        }));
      },
      redirectTo: () => {},
    };
    dispatch("setDataCreateRequest", params);
  };

  const changePaginationPage = (data) => {
    const { model_id, generation_id, page } = data;
    const paramsList = {
      url: "/api_search_parsings/",
      page: page ?? 1,
      page_size: 30,
      model_id,
      generation_id,
      setFieldValue: () => {},
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        if (!!res?.error)
          return setMessageInForm({
            textMessage:
              res?.error ?? "УПС..., возникла не предвиденная ошибка",
            colorMessage: "#ff0000",
          });
        const copyData = res?.result?.results.slice();
        setListAplication((c) => ({
          ...c,
          ...res.result,
          results: [...copyData],
        }));
      },
      redirectTo: (res) => {},
    };
    dispatch("setDataCreateRequest", paramsList);
  };

  const handlerChangeData = (changeData) => {
    let params = {
      url: "/api_get_cars/",
      page_id: state?.action_page_id,
      type: changeData.type,
      dataRequst: changeData.handlerChangeDataRequest,
    };
    if (changeData.brand_id) {
      params = {
        ...params,
        brand_id: changeData.brand_id,
      };
    }
    if (changeData.model_id) {
      params = {
        ...params,
        model_id: changeData.model_id,
      };
    }
    dispatch("getContext", params);
    return false;
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    return () => handlerSubmitSearch(values, setFieldValue);
  };

  return (
    <React.Fragment>
      {dataRequstBrand.length ? (
        <React.Fragment>
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />

          <Formik
            initialValues={{
              optionsBrand: dataRequstBrand,
              optionsModel: [],
              optionsGeneration: [],
              type: state?.type,
              brand_id: "",
              model_id: "",
              generation_id: "",
              text: "",
              uploadFiles: null,
              page: 1,
              page_size: 30,
            }}
            onSubmit={handlerSubmitSearch}
          >
            {({ values, errors, setFieldValue }) => {
              const handlerChangeDataRequest = (data) => {
                let options = getOptions(data.results);
                if (data.type === "model")
                  setFieldValue("optionsModel", options);
                if (data.type === "generation")
                  setFieldValue("optionsGeneration", options);
              };

              return (
                <React.Fragment>
                  <Form
                    style={{
                      width: "100%",
                      position: "relative",
                    }}
                  >
                    <Offset mt={20} mb={20}>
                      {tg.platform === "tdesktop" ? (
                        <SelectDesktop
                          options={values.optionsBrand}
                          value={values.brand_id}
                          placeholder={"сделайте Ваш выбор"}
                          addClass={"select__dropdown-list-car"}
                          name={"brand"}
                          id={1}
                          style={{ zIndex: 998 }}
                          onClick={(e) => {
                            const value = e.target.getAttribute("value");
                            setFieldValue("brand_id", value);
                            setFieldValue("generation_id", "");
                            setFieldValue("model_id", "");
                            setFieldValue("optionsGeneration", []);
                            setDataButtonBottonTrigger(false, tg);
                            handlerChangeData({
                              type: "model",
                              brand_id: value,
                              handlerChangeDataRequest: (res) => {
                                const copyData = res.result.slice();
                                handlerChangeDataRequest({
                                  results: copyData,
                                  type: res.type,
                                });
                              },
                            });
                          }}
                        />
                      ) : (
                        <Select
                          options={values.optionsBrand}
                          value={values.brand_id}
                          placeholder={"сделайте Ваш выбор"}
                          addClass={"select__dropdown-list-car"}
                          name={"brand"}
                          id={1}
                          style={{ zIndex: 998 }}
                          onClick={(e) => {
                            const value = e.target.value;
                            setFieldValue("brand_id", value);
                            setFieldValue("generation_id", "");
                            setFieldValue("model_id", "");
                            setFieldValue("optionsGeneration", []);
                            setDataButtonBottonTrigger(false, tg);
                            handlerChangeData({
                              type: "model",
                              brand_id: value,
                              handlerChangeDataRequest: (res) => {
                                const copyData = res.result.slice();
                                handlerChangeDataRequest({
                                  results: copyData,
                                  type: res.type,
                                });
                              },
                            });
                          }}
                        />
                      )}

                      {tg.platform === "tdesktop" ? (
                        <SelectDesktop
                          options={values.optionsModel}
                          disable={
                            !values.brand_id && !values.optionsModel.length
                          }
                          value={values.model_id}
                          placeholder={"сделайте Ваш выбор"}
                          addClass={"select__dropdown-list-car"}
                          name={"model"}
                          id={2}
                          style={{ zIndex: 997 }}
                          onClick={(e) => {
                            const value = e.target.getAttribute("value");
                            setFieldValue("model_id", value);
                            setFieldValue("generation_id", "");
                            setDataButtonBottonTrigger(false, tg);
                            handlerChangeData({
                              type: "generation",
                              model_id: value,
                              handlerChangeDataRequest: (res) => {
                                const copyData = res.result.slice();
                                handlerChangeDataRequest({
                                  results: copyData,
                                  type: res.type,
                                });
                              },
                            });
                          }}
                        />
                      ) : (
                        <Select
                          options={values.optionsModel}
                          disable={
                            !values.brand_id && !values.optionsModel.length
                          }
                          value={values.model_id}
                          placeholder={"сделайте Ваш выбор"}
                          addClass={"select__dropdown-list-car"}
                          name={"model"}
                          id={2}
                          style={{ zIndex: 997 }}
                          onClick={(e) => {
                            const value = e.target.value;
                            setFieldValue("model_id", value);
                            setFieldValue("generation_id", "");
                            setDataButtonBottonTrigger(false, tg);
                            handlerChangeData({
                              type: "generation",
                              model_id: value,
                              handlerChangeDataRequest: (res) => {
                                const copyData = res.result.slice();
                                handlerChangeDataRequest({
                                  results: copyData,
                                  type: res.type,
                                });
                              },
                            });
                          }}
                        />
                      )}

                      {tg.platform === "tdesktop" ? (
                        <SelectDesktop
                          options={values.optionsGeneration}
                          value={values.generation_id}
                          disable={
                            !values.model_id && !values.optionsGeneration.length
                          }
                          placeholder={"сделайте Ваш выбор"}
                          addClass={"select__dropdown-list-car"}
                          name={"generation"}
                          id={3}
                          style={{ zIndex: 996 }}
                          onClick={(e) => {
                            const value = e.target.getAttribute("value");
                            setFieldValue("generation_id", value);
                            activeButtonBootomForConfirm(
                              tg,
                              { ...values, generation_id: value },
                              "search",
                              "посмотреть"
                            );
                          }}
                        />
                      ) : (
                        <Select
                          options={values.optionsGeneration}
                          value={values.generation_id}
                          disable={
                            !values.model_id && !values.optionsGeneration.length
                          }
                          placeholder={"сделайте Ваш выбор"}
                          addClass={"select__dropdown-list-car"}
                          name={"generation"}
                          id={3}
                          style={{ zIndex: 996 }}
                          onClick={(e) => {
                            const value = e.target.value;
                            setFieldValue("generation_id", value);
                            activeButtonBootomForConfirm(
                              tg,
                              { ...values, generation_id: value },
                              "search",
                              "посмотреть"
                            );
                          }}
                        />
                      )}
                    </Offset>
                    <button
                      className="btn-click"
                      onClick={() => handlerVirtualClick(values, setFieldValue)}
                    ></button>
                    {!!!tg.initDataUnsafe?.query_id ? (
                      <Offset mt={10}>
                        <Button
                          disabled={
                            !(
                              values.generation_id &&
                              values.brand_id &&
                              values.model_id
                            )
                          }
                          type={"submit"}
                          addClass={"form-submit__btn"}
                          active={
                            values.generation_id &&
                            values.brand_id &&
                            values.model_id
                          }
                        >
                          <span>
                            <span>посмотреть</span>
                          </span>
                        </Button>
                      </Offset>
                    ) : null}
                  </Form>
                </React.Fragment>
              );
            }}
          </Formik>
        </React.Fragment>
      ) : (
        <Spinner />
      )}

      {listAplication.results.length && !isLoading ? (
        <React.Fragment>
          <Offset mb={30}>
            <div
              style={{
                color: `var(--text-color)`,
              }}
            >
              Результат поиска: {listAplication?.results.length}
            </div>

            <CardApplication noAction listAplication={listAplication} />

            {listAplication.count > 30 ? (
              <Pagination
                data={listAplication}
                allCount={listAplication.count}
                count={30}
                handlerChangePaginations={changePaginationPage}
                currentPage={Number(listAplication.current_page)}
                location={"center"}
              />
            ) : null}
          </Offset>
        </React.Fragment>
      ) : isLoadingGo ? (
        <Spinner text={`Результат поиска: ${listAplication?.results.length}`} />
      ) : null}
    </React.Fragment>
  );
};
export default CreateSearchParserList;
