import React, { useEffect, useState } from "react";
import { useStoreon } from "storeon/react";
import Spinner from "../../../../Views/Spinner";
import { useLocation, useNavigate } from "react-router-dom";

import Message from "../../../../Views/Messages/Message/Message";
import ListMainButton from "../../../../Views/ListMainButton/ListMainButton";
import { setDataButtonBotton, smoothTop } from "../../../../helpers";

const OurWarrantly = () => {
  const { dispatch, contextOurWarrantly, tg } = useStoreon("contextOurWarrantly", "tg");
  const { state } = useLocation();
  const navigate = useNavigate();
  const { message, buttons } = contextOurWarrantly;
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    state?.textMessage
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: state?.textMessage,
          colorMessage: state?.colorMessage ?? "#555050",
        }))
      : message?.text
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: message.text,
          colorMessage: "#000",
        }))
      : console.log("fake");
  }, [state?.textMessage, message?.text]);

  useEffect(() => {
    setDataButtonBotton(false, tg);
    dispatch("setTextHeader", state?.name_button ?? 'НАШИ ГАРАНТЫ');
    dispatch("setActiveHeaderText", true);

    tg.BackButton.show();
    tg.BackButton.onClick(() =>
      navigate("/button_rating", {
        state: {
          name_button: state?.old_name_button ?? "РЕЙТИНГ И ОТЗЫВЫ",
        },
      })
    );

    const params = {
      url: "/api_get_page/",
      page_id: 6,
      dataRequst: (res) => {
        if (
          res?.result?.info?.status !== undefined &&
          !res?.result?.info?.status
        ) {
          setMessageInForm((c) => ({
            ...c,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
          }));
          smoothTop();
          return;
        }
      },
    };
    dispatch("getContextOurWarrantly", params);

  }, [dispatch, state?.action_page_id, tg.BackButton]);

  return (
    <React.Fragment>
      {"buttons" in contextOurWarrantly ? (
        <React.Fragment>
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />
          <ListMainButton
            old_name_button={state?.name_button}
            localPath={"/button_rating/button_garant_rating/"}
            dispatch={dispatch}
            list={buttons}
          />
        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};

export default OurWarrantly;
