import React, { useEffect, useState } from "react";
import { useStoreon } from "storeon/react";
import Spinner from "../../Views/Spinner";
import { useLocation, useNavigate } from "react-router-dom";

import Message from "../../Views/Messages/Message/Message";
import ListMainButtonMain from "../../Views/ListMainButtonMain/ListMainButtonMain";
import Icon from "../../Views/Icon/Icon";
import { sunDark, sunLight } from "../../images";

const Main = () => {
  const { dispatch, contextMain, tg } = useStoreon("contextMain", "tg");
  const navigate = useNavigate();
  const { state } = useLocation();
  const { message, buttons } = contextMain;
  const [newButtons, setNewButtons] = useState([]);
  const [colorTheme, setColorTheme] = useState("");

  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    message?.name
      ? setMessageInForm((c) => ({
          ...c,
          textMessage: message.name,
          colorMessage: message?.colorMessage ?? "#555050",
        }))
      : console.log("fake");
  }, [message?.name]);

  useEffect(() => {
    tg.BackButton.hide();
    dispatch("setTextHeader", "");
    dispatch("setActiveHeaderText", false);

    const params = {
      url: "/api_get_page/",
      page_id: state?.action_page_id || 1,
      dataRequst: () => {},
    };
    dispatch("getContextMain", params);
  }, [dispatch, state?.action_page_id]);

  useEffect(() => {
    if (!buttons?.length) return;
    setNewButtons([...buttons]);
  }, [buttons]);

  useEffect(() => {
    if (window.sessionStorage.getItem("colorScheme") === "dark") {
      document.documentElement.style.setProperty(
        "--bgc-container-fone",
        tg.colorScheme === "dark" ? tg.themeParams.bg_color : "#17212b"
      );
      document.documentElement.style.setProperty(
        "--bgc-bottom-fone",
        "#19283c"
      );
      document.documentElement.style.setProperty(
        "--bgc-box-menu-fone",
        "#c6c6c6a1"
      );
      document.documentElement.style.setProperty("--text-color", "#ffffff");
      dispatch("setSchemaColor", "dark");
      return setColorTheme((c) => "dark");
    }
    document.documentElement.style.setProperty(
      "--bgc-container-fone",
      "#eef1f6"
    );
    document.documentElement.style.setProperty(
      "--bgc-box-menu-fone",
      "#ffffffcc"
    );
    document.documentElement.style.setProperty("--bgc-bottom-fone", "#ffffff");
    document.documentElement.style.setProperty("--text-color", "#132b45");
    dispatch("setSchemaColor", "light");
    return setColorTheme((c) => "light");
  }, [tg.colorScheme]);

  const handlerClickTheme = () => {
    if (colorTheme === "light") {
      document.documentElement.style.setProperty(
        "--bgc-container-fone",
        tg.colorScheme === "dark" ? tg.themeParams.bg_color : "#17212b"
      );
      document.documentElement.style.setProperty(
        "--bgc-bottom-fone",
        "#19283c"
      );
      document.documentElement.style.setProperty(
        "--bgc-box-menu-fone",
        "#c6c6c6a1"
      );
      document.documentElement.style.setProperty("--text-color", "#ffffff");
      window.sessionStorage.setItem("colorScheme", "dark");
      dispatch("setSchemaColor", "dark");
      return setColorTheme((c) => "dark");
    }
    document.documentElement.style.setProperty(
      "--bgc-container-fone",
      tg.colorScheme === "light"
        ? tg.themeParams.bg_color ?? "#eef1f6"
        : "#eef1f6"
    );
    window.sessionStorage.setItem("colorScheme", "light");
    document.documentElement.style.setProperty(
      "--bgc-box-menu-fone",
      "#ffffffcc"
    );
    document.documentElement.style.setProperty("--bgc-bottom-fone", "#ffffff");
    document.documentElement.style.setProperty("--text-color", "#132b45");
    dispatch("setSchemaColor", "light");
    return setColorTheme((c) => "light");
  };

  return (
    <React.Fragment>
      {"buttons" in contextMain ? (
        <React.Fragment>
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />
          <div
            className="toogle-theme"
            onClick={handlerClickTheme}
            style={{
              justifyContent: colorTheme === "dark" ? "flex-start" : "flex-end",
            }}
          >
            <Icon
              image={colorTheme === "dark" ? sunDark : sunLight}
              zIndex={6}
            />
          </div>
          <ListMainButtonMain
            navigate={navigate}
            localPath={"/"}
            dispatch={dispatch}
            list={newButtons}
          />
        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};

export default Main;
